.App {
  text-align: center;
  width: 90%;
  max-width: 900px;
  margin: auto;
  position: relative;
  margin-bottom: 4rem;
}

.form{
  border-radius: 1rem;
  min-height: 30rem;
  background-image: linear-gradient(to bottom, #09203f 0%, #153853 100%); 
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
